import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ZohoReturnComponent } from './zoho-return/zoho-return.component';
import { ZohoMainComponent } from './zoho-main/zoho-main.component';

const appsRoutes: Routes = [
  {
    path: 'api/zoho',
    component: ZohoMainComponent
  },
  {
    path: 'api/zoho-return',
    component: ZohoReturnComponent
  },
]

@NgModule({
  declarations: [ZohoReturnComponent,ZohoMainComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(appsRoutes)
  ],
  exports:[ZohoMainComponent,ZohoReturnComponent]
})
export class ZohoModule { }
